@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;
  width: 100vw;
  max-width: 500px;

  @extend %page-card;

  .title {
    @include proxima(22px, 600, colors.$foreground_dark);
    padding-bottom: 1em;
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;

    .mat-icon {
      flex-shrink: 0;
    }
  }

  .mat-mdc-card {
    box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.1);
    padding: 20px;
  }

  .currency-label {
    @include proxima(16px, 600, colors.$foreground_dark);
    padding: 0 15px;
    opacity: 0.6;
  }

  .copyable {
    cursor: pointer;

    ::ng-deep {
      input {
        pointer-events: none;
      }
    }
  }

  @media screen and (max-width: 630px) {
    max-width: 400px;
  }

  @media screen and (max-width: 530px) {
    max-width: 280px;
  }
}
